
import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'


export default {
  path: '/studio',
  name: 'Studio',
  meta: {
    title: '工作室管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/studio/list',
  children: [
    {
      path: 'list',
      name: 'StudioIndex',
      meta: {
        title: '工作室列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/studioManage/studioList/Index.vue'], resolve),
    },
    {
      path: 'audite',
      name: 'StudioAudite',
      meta: {
        title: '工作室审核列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/studioManage/studioAudite/Index.vue'], resolve),
    },
    {
      path: 'list',
      meta: {
        title: '工作室列表',
        keepAlive: true
      },
      component: Empty,
      children: [
        {
          path: 'editor',
          name: 'StudioEditor',
          alias: '/studio_editor_3',
          meta: {
            title: '修改工作室',
            keepAlive: true
          },
          component: resolve => require(['@/views/studioManage/studioEditor/Index.vue'], resolve),
        },
        {
          path: 'audite',
          name: 'StudioPreview',
          alias: '/studio_preview_3',
          meta: {
            title: '预览工作室',
            keepAlive: true
          },
          component: resolve => require(['@/views/studioManage/studioPreview/Index.vue'], resolve),
        },

      ]
    },
    {
      path: 'audite',
      meta: {
        title: '工作室审核列表',
        keepAlive: true
      },
      component: Empty,
      children: [
        {
          path: 'add',
          name: 'StudioAdd',
          alias: '/studio_Add_3',
          meta: {
            title: '新增工作室',
            keepAlive: true
          },
          component: resolve => require(['@/views/studioManage/studioEditor/Index.vue'], resolve),
        },
        {
          path: 'audite_status',
          name: 'StatusAudite',
          alias: '/status_audite_3',
          meta: {
            title: '审核',
            keepAlive: true
          },
          component: resolve => require(['@/views/studioManage/auditeStatus/Index.vue'], resolve),
        },
      ]
    },
  ]
}