<template>
    <div class="operator">
        <el-select :value="value" size="small" clearable placeholder="操作人" @change="changeOperator">
            <el-option
                v-for="v in optionArr"
                :key="v.id"
                :label="v.name"
                :value="v.id">
            </el-option>
        </el-select>
    </div>
</template>

<script>
    import goodStorage from "good-storage"

    export default {
        name: 'Operator',
        props: {
            value: String
        },
        created(){
            let operator = goodStorage.session.get(this.$Cfg.operatorSessionKey,[]);
            if(operator.length === 0){
                //这里跳公共的aip
                this.optionArr =[
                    {id:'4',name:'高来贺'},
                    {id:'43',name:'杨帅'},
                    {id:'6',name:'徐森林'},
                    {id:'66',name:'温必猷'},
                ];
                goodStorage.session.set(this.$Cfg.operatorSessionKey,this.optionArr);

            }else {
                this.optionArr = operator;
            }
        },
        data:function () {
            return {
                optionArr:[]
            }
        },
        methods:{
            changeOperator(val){
                this.$emit('input',val+'')
            }
        }
    }
</script>
<style scoped lang="scss">
    .operator{
        display: inline-block;
        margin-right: 6px
    }
</style>
