import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
    path: '/carousel',
    name: 'Carousel',
    meta: {
        title: '轮播图管理',
        keepAlive: false
    },
    component: Layout,
    redirect: '/carousel/carousel_list',
    children: [
        {
            path: 'carousel_list',
            name: 'CarouselList',
            meta: {
                title: '轮播图列表',
                keepAlive: true
            },
            component: resolve => require(['@/views/carouselManage/carouselList/Index.vue'], resolve),
        },
        {
            path: 'carousel_list',
            meta: {
                title: '轮播图列表',
            },
            component: Empty,
            children: [
                {
                    path: 'carousel_edit',
                    name: 'CarouselEdit',
                    meta: {
                        title: '编辑轮播图',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/carouselManage/carouselEdit/Index.vue'], resolve),
                },
            ]
        },
        {
            path: 'carousel_ad',
            name: 'CarouselAd',
            meta: {
                title: '信息流广告列表',
                keepAlive: true
            },
            component: resolve => require(['@/views/carouselManage/ad/Index.vue'], resolve),
        },
        {
            path: 'carousel_ad',
            meta: {
                title: '信息流广告列表',
            },
            component: Empty,
            children: [
                {
                    path: 'ad_edit',
                    name: 'AdEdit',
                    meta: {
                        title: '编辑广告',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/carouselManage/adEdit/Index.vue'], resolve),
                },
            ]
        },
    ]
}