
import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
  path: '/marketing_tools',
  name: 'MarketingTools',
  meta: {
    title: '营销工具管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/marketing_tools/coupon_library',
  children: [
    {
      path: 'coupon_library',
      name: 'MarketingToolsCouponLibrary',
      meta: {
        title: '券池列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/marketingToolsManage/couponLibrary/Index.vue'], resolve),
    },
    {
      path: 'coupon_library',
      meta: {
        title: '券池列表',
      },
      component: Empty,
      children: [
        {
          path: 'coupon_add',
          name: 'MarketingToolsCouponAdd',
          alias:'/marketing_tools_coupon_add_3',
          meta: {
            title: '申请优惠券',
            keepAlive: true
          },
          component: resolve => require(['@/views/marketingToolsManage/couponAdd/Index.vue'], resolve),
        },
      ]
    },
    {
      path: 'coupon',
      name: 'MarketingToolsCoupon',
      meta: {
        title: '优惠券列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/marketingToolsManage/coupon/Index.vue'], resolve),
    },
  ]
}