
import Layout from '../views/layout/App.vue'

export default {
  path: '/label',
  name: 'Label',
  meta: {
    title: '营运标签管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/label/level_one_label',
  children: [
    {
      path: 'level_one_label',
      name: 'LevelOneLabel',
      meta: {
        title: '一级标签列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/labelManage/levelOneLabel/Index.vue'], resolve),
    },
    {
      path: 'level_two_label',
      name: 'LevelTwoLabel',
      meta: {
        title: '二级标签列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/labelManage/levelTwoLabel/Index.vue'], resolve),
    },
    {
      path: 'level_three_label',
      name: 'LevelThreeLabel',
      meta: {
        title: '三级标签列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/labelManage/levelThreeLabel/Index.vue'], resolve),
    },
  ]
}