
import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
  path: '/service',
  name: 'Service',
  meta: {
    title: '服务管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/service/index',
  children: [
    {
      path: 'index',
      name: 'ServiceIndex',
      meta: {
        title: '服务产品列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/serviceManage/index/Index.vue'], resolve),
    },
    {
      path: 'index',
      meta: {
        title: '服务产品列表',
        keepAlive: true
      },
      component: Empty,
      children: [
        {
          path: 'edit',
          name: 'ServiceEdit',
          alias: '/service_edit_3',
          meta: {
            title: '修改服务产品',
            keepAlive: true
          },
          component: resolve => require(['@/views/serviceManage/edit/Edit.vue'], resolve),
        },
        {
          path: 'add',
          name: 'ServiceAdd',
          alias: '/service_add_3',
          meta: {
            title: '新增服务产品',
            keepAlive: true
          },
          component: resolve => require(['@/views/serviceManage/edit/Add.vue'], resolve),
        },
        {
          path: 'preview',
          name: 'ServicePreview',
          alias: '/service_preview_3',
          meta: {
            title: '预览服务产品',
            keepAlive: true
          },
          component: resolve => require(['@/views/serviceManage/preview/Index.vue'], resolve),
        }
      ]
    },
    {
      path: 'sort',
      name: 'ServiceSort',
      meta: {
        title: '服务分类列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/serviceManage/sort/Index.vue'], resolve),
    },
  ]
}