
import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
  path: '/channel',
  name: 'Channel',
  meta: {
    title: '频道管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/channel/nav_list',
  children: [
    {
      path: 'nav_list',
      name: 'ChannelNavList',
      meta: {
        title: '导航列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/channelManage/navList/Index.vue'], resolve),
    },
    {
      path: 'categories_list',
      name: 'ChannelCategoriesList',
      meta: {
        title: '模块分类列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/channelManage/categoriesList/Index.vue'], resolve),
    },
    {
      path: 'categories_list',
      meta: {
        title: '模块分类列表',
      },
      component: Empty,
      children: [
        {
          path: 'category_course_details',
          name: 'categoryCourseDetails',
          meta: {
            title: '模块分类课程列表',
            keepAlive: true
          },
          component: resolve => require(['@/views/channelManage/categoryCourseDetails/Index.vue'], resolve),
        },
      ]
    },





    {
      path: 'studio_list',
      name: 'ChannelStudioList',
      meta: {
        title: '工作室频道列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/channelManage/studioList/Index.vue'], resolve),
    }
  ]
}