
import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
  path: '/msg',
  name: 'Msg',
  meta: {
    title: '消息管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/msg/msg_list',
  children: [
    {
      path: 'msg_list',
      name: 'MsgList',
      meta: {
        title: '消息列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/msgManage/msgList/Index.vue'], resolve),
    },
    {
      path: 'msg_list',
      meta: {
        title: '消息列表',
      },
      component: Empty,
      children: [
        {
          path: 'msg_edit',
          name: 'MsgEdit',
          meta: {
            title: '消息编辑',
            keepAlive: true
          },
          component: resolve => require(['@/views/msgManage/msgEdit/Index.vue'], resolve),
        },
      ]
    },
    {
      path: 'msg_category',
      name: 'msgCategory',
      meta: {
        title: '消息分类',
        keepAlive: true
      },
      component: resolve => require(['@/views/msgManage/msgCategory/Index.vue'], resolve),
    },

  ]
}