
import Layout from '../views/layout/App.vue'
import Empty from "../views/layout/Empty";

export default {
  path: '/users',
  name: 'Users',
  meta: {
    title: '用户管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/users/index',
  children: [
    {
      path: 'index',
      name: 'UsersIndex',
      meta: {
        title: '用户列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/usersManage/index/Index.vue'], resolve),
    },
    {
      path: 'index',
      meta: {
        title: '用户列表',
        keepAlive: false
      },
      component: Empty,
      redirect: '/users/index',
      children: [
        {
          path: 'details',
          name: 'UsersIndexDetails',
          meta: {
            title: '个人详情',
            keepAlive: true
          },
          component: resolve => require(['@/views/usersManage/details/Index.vue'], resolve),
        }
      ]
    }
  ]
}