import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
    path: '/course_pack',
    name: 'CoursePack',
    meta: {
        title: '课程包管理',
        keepAlive: false
    },
    component: Layout,
    redirect: '/course_pack/index',
    children: [
        {
            path: 'index',
            name: 'CoursePackIndex',
            meta: {
                title: '课程包列表',
                keepAlive: true
            },
            component: resolve => require(['@/views/coursePackManage/index/Index.vue'], resolve),
        },
        {
            path: 'index',
            meta: {
                title: '课程包列表',
                keepAlive: true
            },
            component: Empty,
            children: [
                {
                    path: 'details',
                    name: 'CoursePackDetails',
                    alias: '/course_pack_detail_3',
                    meta: {
                        title: '预览课程包',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/coursePackManage/details/Index.vue'], resolve),
                },
                {
                    path: 'editor',
                    name: 'CoursePackEditor',
                    alias: '/course_pack_editor_3',
                    meta: {
                        title: '修改课程包',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/coursePackManage/editor/Index.vue'], resolve),
                },
                {
                    path: 'add',
                    name: 'CoursePackAdd',
                    alias: '/course_pack_add_3',
                    meta: {
                        title: '新增课程包',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/coursePackManage/editor/Index.vue'], resolve),
                },
                {
                    path: 'add',
                    meta: {
                        title: '新增课程包',
                        keepAlive: true
                    },
                    component: Empty,
                    children: [
                        {
                            path: 'select_atom_course',
                            name: 'CoursePackSelectAtomCourse',
                            alias: '/select_atom_course_4',
                            meta: {
                                title: '选择原子课',
                                keepAlive: true
                            },
                            component: resolve => require(['@/views/coursePackManage/selectAtomCourse/Index.vue'], resolve),
                        },
                        {
                            path: 'select_course_pack',
                            name: 'CoursePackSelectCoursePack',
                            alias: '/select_course_pack_4',
                            meta: {
                                title: '选择课程包',
                                keepAlive: true
                            },
                            component: resolve => require(['@/views/coursePackManage/selectCoursePack/Index.vue'], resolve),
                        }
                    ]
                }

            ]
        }
    ]
}