
import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
  path: '/demand_order',
  name: 'DemandOrder',
  meta: {
    title: '发单管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/demand_order/index',
  children: [
    {
      path: 'index',
      name: 'DemandOrderIndex',
      meta: {
        title: '发单列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/demandOrderManage/index/Index.vue'], resolve),
    },
    {
      path: 'index',
      meta: {
        title: '发单列表',
        keepAlive: true
      },
      component: Empty,
      children: [
        {
          path: 'send',
          name: 'SendOrder',
          alias: '/send_order_3',
          meta: {
            title: '发布订单',
            keepAlive: true
          },
          component: resolve => require(['@/views/demandOrderManage/sendOrder/Index.vue'], resolve),
        },
        {
          path: 'preview',
          name: 'PreviewOrder',
          alias: '/preview_order_3',
          meta: {
            title: '预览订单',
            keepAlive: true
          },
          component: resolve => require(['@/views/demandOrderManage/previewOrder/Index.vue'], resolve),
        },
        {
          path: 'pay',
          name: 'PayOrder',
          alias: '/pay_order_3',
          meta: {
            title: '待支付订单',
            keepAlive: true
          },
          component: resolve => require(['@/views/demandOrderManage/payOrder/Index.vue'], resolve),
        },
      ]
    },
  ]
}