import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
    path: '/org',
    name: 'Org',
    meta: {
        title: '机构管理',
        keepAlive: false
    },
    component: Layout,
    redirect: '/org/index',
    children: [
        {
            path: 'index',
            name: 'OrgIndex',
            meta: {
                title: '机构列表',
                keepAlive: true
            },
            component: resolve => require(['@/views/orgManage/index/Index.vue'], resolve),
        },
        {
            path: 'index',
            meta: {
                title: '机构列表',
                keepAlive: true
            },
            component: Empty,
            children: [
                {
                    path: 'add',
                    name: 'OrgAdd',
                    alias: '/org_add_3',
                    meta: {
                        title: '添加机构',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/orgManage/orgEdit/Index.vue'], resolve),
                },
                {
                    path: 'edit',
                    name: 'OrgEdit',
                    alias: '/org_edit_3',
                    meta: {
                        title: '修改机构',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/orgManage/orgEdit/Index.vue'], resolve),
                },
                {
                    path: 'check',
                    name: 'OrgCheck',
                    alias: '/org_check_3',
                    meta: {
                        title: '查看机构',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/orgManage/orgCheck/Index.vue'], resolve),
                }
            ]
        }
    ]
}