import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'
export default {
    path: '/atom_course',
    name: 'AtomCourse',
    meta: {
        title: '原子课管理',
        keepAlive: false
    },
    component: Layout,
    redirect: '/atom_course/index',
    children: [
        {
            path: 'index',
            name: 'AtomCourseIndex',
            meta: {
                title: '原子课列表',
                keepAlive: true
            },
            component: resolve => require(['@/views/atomCourseManage/index/Index.vue'], resolve),
        },
        {
            path: 'index',
            meta: {
                title: '原子课列表',
            },
            component: Empty,
            redirect: '/atom_course/index',
            children: [
                {
                    path: 'details',
                    name: 'AtomCourseDetails',
                    meta: {
                        title: '预览原子课',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/atomCourseManage/details/Index.vue'], resolve),
                },
                {
                    path: 'editor',
                    name: 'AtomCourseEditor',
                    meta: {
                        title: '修改原子课',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/atomCourseManage/editor/Index.vue'], resolve),
                }
            ]
        }
    ],

}