import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
    path: '/article',
    name: 'Article',
    meta: {
        title: '文章管理',
        keepAlive: false
    },
    component: Layout,
    redirect: '/article/article_list',
    children: [
        {
            path: 'article_list',
            name: 'ArticleList',
            meta: {
                title: '文章列表',
                keepAlive: true
            },
            component: resolve => require(['@/views/articleManage/articleList/Index.vue'], resolve),
        },
        {
            path: 'article_list',
            meta: {
                title: '文章列表',
            },
            component: Empty,
            children: [
                {
                    path: 'article_edit',
                    name: 'ArticleEdit',
                    meta: {
                        title: '文章编辑',
                        keepAlive: true
                    },
                    component: resolve => require(['@/views/articleManage/articleEdit/Index.vue'], resolve),
                },
            ]
        },
        {
            path: 'nav_list',
            name: 'ArticleNavList',
            meta: {
                title: '导航列表',
                keepAlive: true
            },
            component: resolve => require(['@/views/articleManage/navList/Index.vue'], resolve),
        },
    ]
}