
import Layout from '../views/layout/App.vue'
import Empty from '../views/layout/Empty.vue'

export default {
  path: '/authentication',
  name: 'Authentication',
  meta: {
    title: '认证管理',
    keepAlive: false
  },
  component: Layout,
  redirect: '/authentication/index',
  children: [
    {
      path: 'index',
      name: 'AuthenticationIndex',
      meta: {
        title: '护士认证列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/authenticationManage/index/Index.vue'], resolve),
    },
    {
      path: 'index',
      meta: {
        title: '护士认证列表',
        keepAlive: true
      },
      component: Empty,
      children: [
        {
          path: 'audite',
          name: 'auditeAuthen',
          meta: {
            title: '认证审核',
            keepAlive: true
          },
          component: resolve => require(['@/views/authenticationManage/operateAuthentication/Audite.vue'], resolve),
        },
        {
          path: 'check',
          name: 'checkAuthen',
          meta: {
            title: '认证预览',
            keepAlive: true
          },
          component: resolve => require(['@/views/authenticationManage/operateAuthentication/Check.vue'], resolve),
        },
      ]
    },
    {
      path: 'authen_list',
      name: 'AuthenList',
      meta: {
        title: '认证列表',
        keepAlive: true
      },
      component: resolve => require(['@/views/authenticationManage/authenList/Index.vue'], resolve),
    },
  ]
}